export default {
	data() {
		return {
			
		}
	},
	methods: {
		fnDownload(){
			uni.downloadFile({
				url: 'https://www.example.com/file/test', //仅为示例，并非真实的资源
				success: (res) => {
					if (res.statusCode === 200) {
						uni.saveFile({
							tempFilePath: res.tempFilePath,
							success: function (res) {
								uni.showToast({
									title: "已下载",
									icon: "none"
								})
							}
						});
					}
				}
			});
		}
	}
}
